.bx--tab-content {
  padding-left: 2px;
  padding-right: 0px;
}

a:hover {
  cursor: pointer;
}

a.bx--header__name {
  font-size: 1.5rem;
  cursor: default;
}

header.bx--header {
  height: 6rem;
}

h2 {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 24px;
}
div.bx--toast-notification.bx--toast-notification--error {
  position: fixed;
  right: 0;
  top: 4rem;
  animation: toast-entry 0.5s ease-in;
  transition: opacity 0.5s linear;
  z-index: 90001;
}

.bx--toast-notification--success {
  position: fixed;
  right: 0;
  top: 4rem;
  animation: toast-entry 0.5s ease-in;
  transition: opacity 0.5s linear;
  z-index: 90001;
}
.TextArea{
  padding-bottom: 24px; 
}
